.ui-paging-container {
  color: #333;
}
.ui-paging-container ul {
  overflow: hidden;
  text-align: center;
  list-style: none;
}
.ui-paging-container li {
  list-style: none;
  display: inline-block;
  vertical-align: middle;
  padding: 3px 6px;
  margin-left: 5px;
  color: #666;
}
.ui-paging-container li.ui-pager {
  border: 1px solid #d3dbe3;
  border-radius: 1px;
  font-size: 12px;
  color: #333;
  cursor: pointer;
  padding: 5px 10px;
}
.ui-paging-container li.ui-pager:hover {
  background: #35afea;
  border: 1px solid #35afea;
  border-radius: 1px;
  color: #fff;
}
.ui-paging-container li.ui-pager-disabled {
  background-color: #f5f5f5;
  cursor: default;
  color: #999;
}
.ui-paging-container li.ui-pager-disabled:hover {
  background-color: #f5f5f5;
  cursor: default;
  border: 1px solid #d3dbe3;
  color: #999;
}
.ui-paging-container li.focus {
  background: #35afea;
  border: 1px solid #35afea;
  border-radius: 1px;
  color: #fff;
}
